import * as React from 'react';
import { useLocation } from 'react-router-dom';
import { useState } from "react";
import { ThemeContext } from "./components/ThemeContext";
import { Provider } from 'react-redux'; //    ,     , 
import { persistor, store } from "./store"; //   ,     src/store/index.jsx
import { PersistGate } from "redux-persist/integration/react";

import { Header } from "./components/common/Header";
import { Navigation } from "./components/common/Navigation";
import { AppRoutes } from './components/common/AppRoutes';
import { useEffect } from 'react';
import { onAuthStateChanged } from 'firebase/auth';
import { auth } from './services/firebase';


export const App = () => {

 const {pathname} = useLocation();

  const setActive = ({ isActive }) =>(isActive ? "navigation__active" : "inactive");
    
  const [theme, setTheme] = useState('dark');

  const [authed, setAuthed] = useState(false);

  const handleLogin = () => {
    setAuthed(true);
  }

  const handleLogout = () => {
    setAuthed(false);
  }

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === 'dark' ? 'light' : 'dark'));
    };

  const setClassName = () => {
    if (pathname === '/chats') {
      return "navigation__active";
    } else {
      return "inactive";
    }
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {  //   ,
      //  auth -   firebase,  ,   
      if(user) {
        handleLogin();
      } else {
        handleLogout();
      }
    });

    return unsubscribe; //    ,          - Unsubscribe
  }, []);

  return (
        
    <PersistGate persistor={persistor}>
      {/*   LOCAL-STORAGE */}

      <Provider store={store}> 
        {/*    */}

        <ThemeContext.Provider value={{theme: theme, toggleTheme: toggleTheme}}>
{/*   value      theme,     theme  
   toggleTheme 
  : {theme, changeTheme: toggleTheme}*/}

          <Header authed={authed} />
          <Navigation setActive={setActive} setClassName={setClassName} />
          <AppRoutes authed={authed}/>
        </ThemeContext.Provider>
      </Provider>
    </PersistGate>
    );
}



