import { ADD_CHAT, DELETE_CHAT, UPDATE_CHATS } from "./actions";

const initialState = [];
 
export const chatsReducer = (state = initialState, { payload, type }) => {
    switch(type) {
        case ADD_CHAT: {
            return [...state, payload];
            // !   payload -  ,    action^
            //   ,   [ ] -        
        }
        case DELETE_CHAT: {
            return state.filter(({ id }) => id !== payload);
        }
        case UPDATE_CHATS: {
          return [...payload];
          
      }
        default:
            return state;
    }
};

